import axios from 'axios'
import qs from 'qs'

export const state = {
  loading: true,
  pageInfo: {},
  records: [],
  currentRecord: {},
}

export const getters = {
  sub_title: (blog) => blog.owner.username + ' ' + blog.create_time,
}

export const mutations = {
  BLOG_LIST(state, payload) {
    state.pageInfo = payload.page_info
    state.records = payload.records
    state.loading = false
  },
  BLOG_LOAD_MORE(state, payload) {
    state.pageInfo = payload.page_info
    state.records.push(...payload.records)
    state.loading = false
  },
  BLOG_GET(state, payload) {
    state.currentRecord = payload.record
    state.loading = false
  },
}

export const actions = {
  list(ctx, { query }) {
    return axios
      .get(
        `/api/blogs/?${qs.stringify(
          Object.assign({ per_page: 5, page: 1 }, query)
        )}`
      )
      .then((response) => {
        const resp = response.data
        ctx.commit('BLOG_LIST', resp)
        return resp
      })
  },
  get(ctx, { pk }) {
    return axios.get(`/api/blogs/${pk}/`).then((response) => {
      const resp = response.data
      ctx.commit('BLOG_GET', resp)
      return resp
    })
  },
  loadMore(ctx, state) {
    console.log(
      ctx,
      Object.assign(
        { per_page: 5, page: 1 },
        (ctx.state.pageInfo.page || 1) + 1
      )
    )
    return axios
      .get(
        `/api/blogs/?${qs.stringify(
          Object.assign({
            per_page: 5,
            page: (ctx.state.pageInfo.page || 1) + 1,
          })
        )}`
      )
      .then(
        (response) => {
          const resp = response.data
          ctx.commit('BLOG_LOAD_MORE', resp)
          state.loaded()
          return resp
        },
        () => state.complete()
      )
  },
}
