import axios from 'axios'
import qs from 'qs'

export const state = {
  loading: true,
  pageInfo: {},
  records: [],
}

export const getters = {}

export const mutations = {
  NAME_LIST(state, payload) {
    state.pageInfo = payload.page_info
    state.records = payload.records
    state.loading = false
  },
  NAME_LIST_CHANGED(state, obj) {
    const index = state.records.findIndex((item) => item.id === obj.id)
    console.log(state, index, obj)
    state.records.splice(index, 1, obj)
  },
}

export const actions = {
  fetchNames(ctx, { query }) {
    // console.log('fetchNames ctx', ctx)
    // console.log('fetchNames query', query)
    return axios
      .get(
        `/api/name/?${qs.stringify(
          Object.assign({ per_page: 5, page: 1 }, query)
        )}`
      )
      .then((response) => {
        // console.log('fetchNames 1111111111', response.data)
        const resp = response.data
        ctx.commit('NAME_LIST', resp)
        return resp
      })
  },
  toggle_marked(ctx, item) {
    // console.log(ctx, item)
    return axios
      .put(`/api/name/${item.id}/marked/`, { marked: !item.marked })
      .then((response) => {
        const resp = response.data
        console.log('resp=', resp)
        // item.marked = resp.marked
        // ctx.commit('BLOG_GET', resp)
        ctx.commit('NAME_LIST_CHANGED', resp)
        return resp
      })
  },
}
